import List from "./components/BookList";
import Create from "./components/BookCreate";
import { useState, useEffect} from "react";
import axios from "axios";

function App() {
  const [content, setContent] = useState([]);

  const fetchBooks = async () =>{
    const response = await axios.get('http://localhost:3001/books');
    setContent(response.data);
  }

  useEffect(() => {
    fetchBooks();
  }, [])



  const DeleteHandler = async(term) => {
    await axios.delete(`http://localhost:3001/books/${term}`)

    const updatedItems = content.filter((element,index) => {
      return element.id !== term
    })
    setContent(updatedItems);
  }

  const setItemHandler = async (term) =>{
    const response = await axios.post('http://localhost:3001/books', {
      term,
    })

    const updatedItems = [response.data, ...content]
    setContent(updatedItems);
    console.log(content)
  }

  const book_id_handler = async(new_title, remove_id) => {

    const response = await axios.put(`http://localhost:3001/books/${remove_id}`, {
      term: new_title
    } );

    console.log(response)
    const updatedArray = content.map(contents => {
      if(contents.id === remove_id){
        return { ...contents, ...response.data};
      }
      return contents;
      });
      console.log('HELlo', new_title + remove_id);
    setContent(updatedArray);
    
  }

  return (
    <div className="">
      <div className="bg-sky-400 h-fit">
        <h1 className="text-white font-bold text-2xl pl-5">BookList  <span className="text-[15px] font-thin">(App is on a json-server, web application is not useful as of the moment)</span></h1>
      </div>
    <Create setItem={setItemHandler}/>
    <List content={content} deleteByID={DeleteHandler} book_id={book_id_handler}/>
    </div>
  );
}

export default App;
