
import React, { StrictMode } from 'react'
import BookShow from './BookShow'
import { useState } from 'react'
import BookUpdate from './BookUpdate'

function List({content , deleteByID, book_id}) {
  
  const ListofItems = content.map((contents, index)=> {
    return(
      <div className='mx-auto'>
      <BookShow title={contents.term} content_id={contents.id} contents_title={contents.term} deleteByID={contents.id} deleteByIDtoParent={deleteByID} book_id={book_id}/>

      </div>
    );
  })
  return (
    <div className='grid lg:grid-cols-5 gap-7 place-content-center p-5 md:grid-cols-3 sm:grid-cols-1'>{ListofItems}</div>
  )
}

export default List

